/* Normal: 5cm x 2.4cm = 142px x 68px */
/* Scaled: 580px x 277px */

@font-face {
  font-family: "LibreBarcode39-Regular";
  src: url("/assets/fonts/libre_barcode_39/LibreBarcode39-Regular.ttf");
}

@font-face {
  font-family: "LibreBarcode128-Regular";
  src: url("/assets/fonts/libre_barcode_128/LibreBarcode128-Regular.ttf");
}

$barCodeFont: "LibreBarcode39-Regular";

.Preview {
  align-items: center;
  border: 1px solid #000;
  border-radius: 1em;
  display: flex;
  font-size: 2.1em;
  height: 368px;
  margin-bottom: 2%;
  max-width: 580px;
  padding: .35em .35em;
  width: 100%;

  img {
    display: block;
    height: auto;
    max-width: 100%;
  }

  .info {
    margin-bottom: .8em;
  }

  .part-number {
    font-weight: bold;
  }

  .name {
    line-height: 100%;
  }

  .ce-logo {
    align-items: center;
    display: flex;
    margin-bottom: .15em;

    .logo {
      flex-shrink: 0;
      width: 1.25em;

      svg {
        display: block;
        height: auto;
        max-width: 100%;
      }
    }

    div:nth-child(2) {
      font-size: .6em;
      padding-left: .8em;
    }
  }

  .barcode {
    text-align: center;

    svg {
      display: block;
      height: auto;
      margin: 0 auto;
      width: 75%;
    }
  }

  .side:nth-child(1) {
    flex-shrink: 0;
    width: 70%;
  }

  .side:nth-child(2) {
    align-items: center;
    border-left: 2px solid #000;
    display: flex;
    height: 90%;
    justify-content: center;
    width: 30%;

    .barcode {
      margin-top: 85%;
      left: 2%;
      position: relative;
      transform: rotateZ(-90deg);

      svg {
        width: 185%;
      }
    }
  }
}

