/*
    Button
 */
.Button {
  appearance: none;
  background-color: #020252;
  border: none;
  border-radius: 2px;
  box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12), 0 1px 5px 0 rgba(0,0,0,0.2);
  color: #fff;
  cursor: pointer;
  font-size: 14px;
  height: 45px;
  line-height: 45px;
  margin: 0 5px;
  outline: none;
  overflow: hidden;
  padding: 0 15px;
  position: relative;
  text-align: center;
  transition: background-color 375ms;
}
