/*
    Alert
 */
.Alert {
  border: 1px solid transparent;
  border-radius: .25rem;
  margin-bottom: .5rem;
  margin-top: .5rem;
  padding: .75rem 1.25rem;
  text-align: center;

  &.Alert-Error {
    background-color: #ffd2d2;
    border-color: #ecbbbb;
    color: #af4b4b;
  }

  &.Alert-No-Visible {
    display: none !important;
    visibility: hidden !important;
  }
}
