/*
    Result Item
 */
.ResultItem {
  align-items: center;
  background-color: transparent;
  cursor: pointer;
  display: flex;
  padding: 1% 1%;
  transition: background-color 375ms;

  &:hover,
  &.hover {
    background-color: #cacaca;
  }

  .Image {
    width: 10%;
  }

  .Info {
    font-style: italic;
    font-size: .8em;
    padding-left: 1%;
    width: 85%;
  }
}
