/*
    Searcher
 */
#Searcher {
  max-width: 580px;
  margin-bottom: 2%;
  position: relative;
  width: 100%;
  z-index: 1;

  h1 {
    font-size: 3em;
    margin-top: 0;
    text-align: center;
    width: 100%;
  }

  .input-container {
    position: relative;
  }

  input {
    border: 1px solid #cacaca;
    display: block;
    font-family: inherit;
    font-size: 1.5em;
    padding: .50em;
    outline: none;
    width: 100%;
  }

  // Loading State
  &.loading .input-container:after {
    background: url('/assets/images/loader.svg');
    background-size: 2.5em 2.5em;
    bottom: .4em;
    content: "";
    display: block;
    height: 2.5em;
    position: absolute;
    right: .4em;
    width: 2.5em;
  }

}
